
.u-checkbox .el-checkbox__input.is-checked .el-checkbox__inner,
.u-checkbox .el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #555;
  border-color: #555;
}
.u-checkbox .el-checkbox__input .el-checkbox__inner:hover {
  border-color: #555;
}
.u-checkbox .el-checkbox {
  font-size: inherit;
}
.u-checkbox .el-checkbox__label {
  font-weight: 400;
  font-size: inherit;
  color: #555;
}
.u-checkbox .el-checkbox__input.is-checked + .el-checkbox__label {
  color: #555;
}



