
.el-select {
  width: 100%;
}
.el-select .el-select-dropdown__list {
    padding: 0px 8px;
}
.el-select .el-input__inner {
    padding-top: 3px;
    padding-bottom: 3px;
}
.el-select .el-select__input {
    padding-left: 5px;
    margin-left: 0px;
    max-width: 30px !important;
}
.el-select .el-input__inner,
  .el-select .el-select-dropdown__item {
    font-size: 12px;
    height: initial !important;
    line-height: 20px;
}
.el-select .el-select__tags {
    width: calc(100% - 35px);
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}
.el-select .el-select__tags > span {
      width: 100%;
}
.el-select .el-select__tags .el-select__tags-text {
      font-size: 0.9em;
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
}
.el-select .el-select__tags .el-tag--small {
      height: 17px;
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.el-select .el-select__tags .el-tag--small:first-child {
        max-width: calc(100% - 50px);
}
.el-select .el-select__tags .el-tag--small .el-tag__close {
        top: 1px;
}



