
.el-radio-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-weight: inherit;
  font-size: inherit;
}
.el-radio {
  font-size: inherit;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-weight: inherit;
}
.el-radio__label {
    font-size: inherit;
}
.el-radio .el-radio__input.is-checked + .el-radio__label {
    color: #555;
}
.el-radio .el-radio__input.is-checked .el-radio__inner {
    border-color: #555;
    background: #555;
}



