
.el-table-filter__content {
  overflow-y: auto;
  max-height: 300px;
}
.el-table-filter__content .el-checkbox__input.is-checked .el-checkbox__inner,
  .el-table-filter__content .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #555;
    border-color: #555;
}
.el-table-filter__content .el-checkbox__input.is-checked + .el-checkbox__label {
    color: #555;
}
.el-table-filter__content .el-checkbox__label {
    font-weight: normal;
    font-size: 12px;
}
.el-table {
  height: 100%;
}
.el-table .table-header .cell {
    word-break: keep-all;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.el-table th > .cell.highlight {
    color: #555;
}
.el-table .table-body .cell {
    word-break: keep-all;
}
.el-table .caret-wrapper .sort-caret {
    right: 3px;
    left: auto;
}
.el-table .descending .sort-caret.descending {
    border-top-color: #555;
}
.el-table .ascending .sort-caret.ascending {
    border-bottom-color: #555;
}



